
.badge-error {
  background-color: $danger;
}
.badge-error:hover {
  background-color: $danger_highlight;
}
.badge-warning {
  background-color: $warning;
}
.badge-warning:hover {
  background-color: $warning_highlight;
}
.badge-success {
  background-color: $success;
}
.badge-success:hover {
  background-color: $success_highlight;
}
.badge-info {
  background-color: $primary;
}
.badge-info:hover {
  background-color: $primary_highlight;
}
.badge-inverse {
  background-color: $inverse;
}
.badge-inverse:hover {
  background-color: $inverse_highlight;
}
.badge-red {
	background-color:$red;
}
.badge-red:hover {
	background-color:$red_highlight;
}

.badge-small {
	font-size:11px;
	padding:3px 7px;
